.countdown {
  color: #fff;
  font-size: 32px;
  padding: 24px 54px;
  text-align: center;
  border-radius: 10px;
  background: #2770c3;
  filter: drop-shadow(0px 8px 15px rgba(0, 0, 0, 0.18));
  background: linear-gradient(-90deg, #2f90ff 0%, #2770c3 100%);

  &__title {
    font-size: inherit;
    font-family: "Luckiest Guy";
  }
}
